[mol_theme] {
  --bw_search_menu-mol_check-border-light: 4px solid color(#313745 a(20%));
  --bw_search_menu-mol_check-border-dark: 4px solid rgba(255, 255, 255, 0.2);

  --bw_search_menu-mol_check-color-light: #313745;
  --bw_search_menu-mol_check-color-dark: white;

  --bw_search_menu-mol_check-checked-border-light: 4px solid color(#313745 a(50%));
  --bw_search_menu-mol_check-checked-border-dark: 4px solid white;
}

$bw_search_padding_left: 0px;

[bw_search_menu] {
  $left_space: calc(45px - $bw_search_padding_left);
  margin: 24px 0 10px $left_space;
  flex: 0 0 auto;
  box-shadow: none;

  [mol_check] {
    text-transform: uppercase;
    font-size: 18px;
    padding: 0 12px;

    border-bottom: var(--bw_search_menu-mol_check-border);
    line-height: normal;
    height: 21px;
    padding-bottom: 5px;
    box-sizing: content-box;
    margin-bottom: 0;
    font-weight: var(--bw-font-regular-weight);
    color: var(--bw_search_menu-mol_check-color);

    align-items: center;
    flex-direction: row-reverse;

    svg {
      width: 13px;
      margin-left: 2px;
      position: relative;
      top: -6px;
      path {
        stroke: color(#313745 a(0.5))
      }
    }

    &[bw_search_menu_option_new] svg {
      margin-left: 4px;
      top: 0px;
    }

    &:hover {
      /* TODO */
    }

    &[mol_check_checked] {
      background-color: inherit;
      font-weight: var(--bw-font-medium-weight);
      border-bottom: var(--bw_search_menu-mol_check-checked-border);
    }
  }
}

[bw_search] {
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow: hidden;

  [bw_block] {
    & {
      margin-left: 16px;
    }

    &:last-child {
      margin-right: 16px;
    }
  }

  [bw_search_content] {
    flex: 1 1 auto;
    display: block;
    overflow: hidden;
    display: flex;
    flex-direction: column;
  }
  [bw_search_workspace] {
    flex: 1 1 auto;
    /* width: 100%; */
    /* height: 100%; */
    display: flex;
    flex-direction: column;
  }

  [bw_search_new] {
    display: block;
    padding-right: 0;
    overflow: auto;
    position: relative;

    display: flex;
    flex-wrap: nowrap;

    [bw_block_inner] {
      display: flex;
      flex-direction: column;
      [bw_block_header] {
        flex: 0 0 69px;
        padding-top: 32px;
        box-sizing: border-box;
        padding-left: 8px;

        display: flex;
        flex-direction: column;
        justify-content: flex-start;

        [bw_block_caption] {
          text-transform: uppercase;
          height: 21px;
          font-weight: var(--bw-font-medium-weight);
          font-size: 18px;
          box-sizing: content-box;
        }
      }
      [bw_block_content] {
        flex: 1 1 auto;
        display: flex;
        flex-direction: column;
        /* justify-content: space-between; */

        [bw_field] {
          display: flex;
          justify-content: space-between;
        }
      }
      [bw_block_buttonbar] {
        flex: 0 0 32px;
        box-sizing: border-box;
        box-shadow: none;
        display: flex;
        align-items: center;
        justify-content: center;

        & > *:first-child {
          width: 200px;
          box-sizing: border-box;
        }
      }
    }

    [bw_block] {
      $block-min-width: 536px;
      $block-max-width: 536px;
      flex: 1 0 $block-min-width;
      max-width: $block-max-width;
      height: 715px;
      box-sizing: border-box;
      display: flex;
      justify-content: flex-start;

      $block-horspace-min-width: 16px;
      $block-horspace-max-width: 16px;
      & > horspace {
        flex: 1 0 $block-horspace-min-width;
        max-width: $block-horspace-max-width;
        box-sizing: border-box;
        display: block;
      }
      [bw_block_inner] {
        $inner-min-width: calc($block-min-width - 2 * $block-horspace-min-width);
        $inner-max-width: calc($block-max-width - 2 * $block-horspace-max-width);
        flex: 1 0 $inner-min-width;
        max-width: $inner-max-width;
        box-sizing: border-box;
        $field-control-min-width: 335px;
        $field-control-max-width: 354px; /* TODO: use in calc for flex of & > *:last-child */

        [bw_field] {
          & > *:first-child {
            flex: 1 0 calc($inner-min-width - $field-control-min-width);
          }
          & > *:last-child {
            flex: 0.731 0 $field-control-min-width;
          }
        }
      }
    }
  }
}

[bw_search_block_table] {
  border-collapse: collapse;
  &:not(:last-of-type) {
    margin-bottom: 16px;
  }
  $width-first-col: 194px;
  [bw_search_block_table_head] {
    background-color: #d8dce3;
    th {
      height: 40px;
      font-family: BwRoboto;
      font-size: 16px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      letter-spacing: normal;
      color: #313745;
    }
    th:first-of-type {
      text-align: left;
      padding-left: 8px;
      width: $width-first-col;
    }
  }
  [bw_search_block_table_body] {
    tr {
      cursor: pointer;
    }
    td {
      height: 32px;
      background-color: #f0f1f4;
      padding: 0;
      border-top: 1px solid white;
    }
    td:first-of-type {
      padding-left: 8px;
      color: #53adff;
      width: $width-first-col;
    }
    td:not(:first-of-type) {
      text-align: center;
      color: color(#6a6c74 a(0.7))
    }
    td:nth-of-type(2) {
      color: #6a6c74;
    }
  }
}

[bw_search_params] {
  flex: 0 0 auto;
  background: white;
  margin: 0 16px 16px;
  position: relative;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden;
  [bw_search_params_input] {
    width: 314px;
    top: 16px;
    left: 16px;
    position: absolute;
  }
  [bw_search_params_breadcrumb] {
    position: absolute;
    color: #6a6c74;
    border-radius: 3px;
    border: solid 1px #bdc3d1;
    background-color: #fcfcfd;
    padding: 1px 18px 1px 8px;
    font-size: 14px;
    height: 24px;
    box-sizing: border-box;
  }
  [bw_search_params_iconclose] {
    cursor: pointer;
    display: block;
    position: absolute;
    width: 12px;
    right: 4px;
    top: 6px;
    opacity: 0.5;
    path {
      stroke: #313745;
    }
  }
  [bw_search_params_content] {
    margin-top: 60px;
    flex: 1 1 auto;
    position: relative;
    font-size: 14px;
  }
  [bw_input] {
    padding: 4px 7px;
    height: 24px;
    box-sizing: border-box;
    font-size: 14px;
    border-radius: 3px;
    border: solid 1px #bdc3d1;
    background-color: #fcfcfd;
    &::placeholder {
      opacity: 0.5;
      font-size: 14px;
    }
  }


  [bw_input_combo] {
    height: 24px;
    width: 100%;
    $check-padding: 4px;
    $icon-width: 9px;
    [bw_input_combo_input] {
      padding: 4px 7px;
      font-size: 14px;
      border-radius: 3px;
      border: solid 1px #bdc3d1;
      background-color: #fcfcfd;
    }
    [bw_input_combo_check] {
      width: calc($icon-width + $check-padding + 10px);
      padding-right: 4px;
    }
    [bw_input_combo_icon] {
      width: $icon-width;
      path {
        fill: #444956;
      }
    }
  }
  [bw_search_params_footer] {
    flex: 0 0 auto;
    font-size: 16px;
    font-weight: var(--bw-font-medium-weight);
    line-height: normal;
    color: #313745;
    margin: 16px;
    text-transform: uppercase;
  }

  &[bw_search_params_mode = "full"] {
    min-height: 627px;
  }
  &[bw_search_params_mode = "main"] {
    min-height: 295px;
  }
  &[bw_search_params_mode = "short"] {
    min-height: 100px;
  }

  [bw_search_params_mode] {
    position: absolute;
    top: 0;
    right: 0;
    box-shadow: none;
    [mol_check] {
      padding: 20px 8px 8px;
      text-transform: uppercase;
      font-weight: var(--bw-font-regular-weight);
      color: #0070a4;
      font-size: 14px;
    }
    [mol_check_checked] {
      background: #0070a4;
      color: white;
      font-weight: var(--bw-font-medium-weight);
    }
  }
}

[bw_search_params_block] {
  position: absolute;
  display: block;
  left: 16px;
  top: 0;
  display: flex;
  flex-direction: column;

  [mol_check_box] {
    display: flex;
    align-items: center;
    padding: 0 0 0 0;
    color: #6a6c74;
    [mol_check_box_icon] {
      width: 14px;
      height: 14px;
      box-sizing: border-box;
      border-radius: 2px;
      border: solid 1px #313745;
      margin: 0;
    }
    [mol_check_title] {
      margin-left: 12px;
      white-space: nowrap;
      overflow: visible;
    }
    &[mol_check_checked] {
      [mol_check_box_icon] {
        background: #0070a4;
        border: solid 1px #0070a4;
      }
    }
  }

  [bw_search_params_block_upper] {
    flex: 0 0 calc(184px - 104px - 16px);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    & > * {
      flex: 0 0 24px;
      display: flex;
      justify-content: space-between;
      & > *:nth-child(1) {

        flex: 0 0 316px;
        max-width: 316px;
        [bw_search_params_field_control] {
          & > * {
            max-width: 100px;
          }
        }
      }
      & > *:nth-child(2) {
        flex: 0 0 calc(316px * 2 + 84px);
        max-width: calc(316px * 2 + 84px);
      }
      & > *:nth-child(3) {
        flex: 0 0 316px;
        max-width: 316px;
      }
    }
  }

  [bw_search_params_block_downer] {
    flex: 1 1 auto;
    display: flex;
    justify-content: space-between;
    & > * {
      flex: 0 0 316px;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      & > * {
        margin-top: 16px;
        flex: 0 0 24px;
      }
    }
  }
  [bw_search_params_block_roomqt] {
    [mol_switch] {
      box-shadow: none;
      border: solid 1px #bdc3d1;
      border-radius: 3px;
    }
    [mol_check] {
      width: 36px;
      height: 24px;
      box-sizing: border-box;
      padding: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      color: #313745;
      font-size: 14px;
      background: #fcfcfd;
    }
    [mol_check]:hover {
      border: solid 1px color(#008ecf a(0.5));
      background-color: color(#f0f1f4 a(0.5));
    }
    [mol_check][mol_check_checked] {
      background: #f0f1f4;
      border-radius: 3px;
      border: solid 1px #008ecf;
    }
  }
  [bw_search_params_block_location] {
    [mol_switch] {
      box-shadow: none;
      border: solid 1px #bdc3d1;
      border-radius: 3px;
    }
    [mol_check], [mol_check_checked] {
      font-size: 14px;
      padding: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #fcfcfd;
      color: #0070a4;
    }
    [mol_check]:not(:last-child) {
      border-right: solid 1px #bdc3d1;
    }
    [mol_check]:nth-child(1) {
      flex: 0 1 59px;
    }
    [mol_check]:nth-child(2) {
      flex: 0 1 43px;
    }
    [mol_check]:nth-child(3) {
      flex: 0 1 63px;
    }
    [mol_check]:nth-child(4) {
      flex: 1 1 auto;
    }
  }
}

[bw_search_params_block_main] {
  height: 184px;
  width: 1117px;
}

[bw_search_params_block_full] {
  height: calc(627px - 60px - 50px);
  width: 1516px;
  [bw_search_params_block_full_geotuning] {
    display: flex;
    & > *:first-child { margin-right: 16px }
  }
  [bw_search_params_block_full_source_control] {
    height: 100%;
    widht: 100%;
  }
  [bw_search_params_block_full_sourcecontrol] {
    height: calc(14px * 4 + 16px * 3);
  }
}

[bw_search_param_source] {
  display: flex;
  justify-content: space-between;
  & > * {
    flex: 0 0 100px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
}

[bw_search_params_label] {
  font-weight: var(--bw-font-medium-weight);
}

[bw_search_params_input_commitable] {
  height: 24px;
  [bw_input_checkable_check] {
    width: calc(4px + 16px + 8px);
    padding: 4px;
    [bw_input_checkable_icon] {
      width: 16px;
      height: 16px;
      opacity: 0.5;
      background-color: #0070a4;
      border-radius: 2px;
      path {
        stroke: white;
      }
    }
  }
}

[bw_search_param_diap] {
  display: flex;
  justify-content: space-between;
  & > * {
    flex: 0 0 100px;
  }
}

[bw_search_params_field] {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  [bw_search_params_field_label] {
    flex: 0 0 98px;
  }
  [bw_search_params_field_control] {
    flex: 1 1 auto;
    justify-content: space-between;
    display: flex;
    & > * {
      flex: 0 0 100px;
      max-width: 100px;
    }
    & > *:last-child:first-child {
      flex: 1 1 auto;
      max-width: none;
    }
  }
}

[bw_search_result] {
  flex: 1 1 auto;
  background: white;
  margin: 0 16px;
  display: flex;
  flex-direction: column;
  overflow: hidden;

  [bw_search_result_map] {
    display: block;
  }
  [bw_search_result_header] {
    flex: 0 0 auto;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    margin: 0 16px 16px;
  }
  [bw_workspace_search_result_title] {
    text-transform: uppercase;
    line-height: 30px;
    font-weight: var(--bw-font-medium-weight);
    color: #313745;
  }
  [bw_workspace_search_result_iconfilter] {
    width: 18px;
    margin-left: 8px;
    cursor: pointer;
    path {
      fill: transparent;
      stroke: color(#0070a4 a(0.7));
    }
  }
  [bw_search_result_avg] {
    font-size: 14px;
    line-height: 30px;
    margin-left: 80px;
    *:not(:last-of-type) {
      margin-right: 16px;
    }
  }
  [bw_search_result_avg] {
    font-size: 14px;
    line-height: 30px;
    margin-left: 80px;
    *:not(:last-of-type) {
      margin-right: 16px;
    }
  }
  [bw_workspace_search_result_toolbar] {
    flex: 0 1 300px;
    display: flex;
    justify-content: center;
    margin-bottom: 6px;
    svg:not(:last-of-type) {
      margin-right: 20px;
    }
    svg {
      cursor: pointer;
      width: 18px;
      height: 18px;
      fill: color(#0070a4 a(0.7));
    }
  }

  [bw_search_result_space] {
    flex: 1 1 auto;
    olor: #313745;
  }
  [bw_search_result_view] {
    display: flex;
    align-items: center;
    margin: 0px 16px;
    & > *:first-child {
      font-size: 14px;
      line-height: 30px;
      margin-right: 16px;
    }
    & > *:last-child {
      height: 24px;
      width: 111px;
    }
    [bw_input_checkable_input], [mol_check_title] {
      font-size: 14px;
    }
  }

  [bw_search_result_mode] {
    box-shadow: none;
    flex: 0 0 auto;
    [mol_check] {
      padding: 20px 16px 5px;
      text-transform: uppercase;
      font-weight: var(--bw-font-regular-weight);
      color: #0070a4;
      font-size: 14px;
    }
    [mol_check_checked] {
      background: #0070a4;
      color: white;
      font-weight: var(--bw-font-medium-weight);
    }
  }
  [bw_search_result_mapon] {
    flex-direction: row-reverse;
    color: #0070a4;
    font-size: 14px;
    text-transform: uppercase;
    padding: 20px 8px 5px;
    svg {
      height: 17px;
      margin: 0;
      margin-left: 8px;
      margin-top: 1px;
      path {
        fill: #0070a4;
      }
    }
    margin-left: 16px;
  }
  [bw_search_result_content] {
    flex: 1 1 auto;
    margin: 0 16px;
    position: relative;
    /* overflow: hidden; */
  }
  [bw_search_result_grid] {
    position: absolute;
    width: 100%;
  }
}

