
body[_theme="dark"] bw_login {

  --bw-login-check-box-title-color: #b4bccd;
  --bw-login-check-box-title-color-hover: white;
  --bw-login-check-box-title-color-focus: var(--bw-login-check-box-title-color-hover);
  --bw-login-check-box-background-color: white;
  --bw-login-check-box-background-color-checked: white;
  --bw-login-check-box-tick-color: #363636;
  --bw-login-check-box-border: transparent;
  --bw-login-check-box-border-hover: var(--bw-login-check-box-border);
  --bw-login-check-box-border-focus: var(--bw-login-check-box-border);
  --bw-login-check-box-border-checked: transparent;

  --bw-login-link-color: #53adff;

  --bw-login-background-color: #414c5f;
  --bw-logo-blue: white;
  --bw-logo-cyan: white;

  --bw-login-switch-option-border-bottom: solid rgba(255, 255, 255, 0.3) 3px;
  --bw-login-switch-option-border-bottom-hover: solid rgba(255, 255, 255, 0.53) 3px;
  --bw-login-switch-option-border-bottom-accent: solid rgba(255, 255, 255, 1) 3px;
  --bw-login-switch-title-text-color: rgba(255, 255, 255, 0.5);
  --bw-login-switch-title-text-color-hover: rgba(255, 255, 255, 0.67);
  --bw-login-switch-title-text-color-accent: rgba(255, 255, 255, 1);

  --bw-login-form-close-icon-color: white;

  --bw-login-input-placeholder-text-color: rgba(255, 255, 255, 0.7);
  --bw-login-input-placeholder-text-color-hover: white;;
  --bw-login-input-placeholder-text-color-focus: var(--bw-login-input-placeholder-text-color-hover);
  --bw-login-input-text-color: white;
  --bw-login-input-background-color: #878f9b;
  /*--bw-login-input-background-color-hover: rgba(135, 143, 155, 0.5);*/
  --bw-login-input-background-color-hover: #636D7B;
  --bw-login-input-background-color-focus: var(--bw-login-input-background-color-hover);
  --bw-login-input-border: 1px solid #d8dce3;
  --bw-login-input-border-hover: 1px solid white;
  --bw-login-input-border-focus: var(--bw-login-input-border-hover);
  --bw-login-input-box-shadow: inset 0 2px 4px 0 rgba(0, 0, 0, 0.25);

  --bw-login-input-icon-color: var(--bw-login-input-placeholder-text-color);
  --bw-login-input-icon-color-hover: var(--bw-login-input-placeholder-text-color-hover);

  --bw-login-pass-icon-color: var(--bw-login-input-placeholder-text-color);
  --bw-login-pass-icon-color-hover: var(--bw-login-input-placeholder-text-color-hover);
  --bw-login-pass-icon-color-focus: var(--bw-login-input-placeholder-text-color-focus);

  --bw-login-button-background-color: #008ecf;
  --bw-login-button-box-shadow-accent: 0 8px 4px 0 rgba(0, 0, 0, 0.25);

}

body[_theme="light"] bw_login {
  --bw-login-check-box-title-color: #313745;
  --bw-login-check-box-title-color-hover: #363d4e;
  --bw-login-check-box-title-color-focus: var(--bw-login-check-box-title-color-hover);
  --bw-login-check-box-background-color: rgba(0,0,0,0);
  --bw-login-check-box-background-color-checked: #0070a4;
  --bw-login-check-box-tick-color: white;
  --bw-login-check-box-border: 1px solid #bdc3d1;
  --bw-login-check-box-border-hover: 1px solid #363d4e;
  --bw-login-check-box-border-focus: var(--bw-login-check-box-border-hover);
  --bw-login-check-box-border-checked: var(--bw-login-check-box-background-color-checked);

  --bw-login-link-color: #2b87db;

  --bw-login-background-color: white;
  --bw-logo-blue: rgb(16,16,119);
