  --bw-login-switch-option-border-bottom: solid rgba(49, 55, 69, 0.5) 3px;
  --bw-login-switch-option-border-bottom-hover: solid rgba(49, 55, 69, 0.67) 3px;
  --bw-login-switch-option-border-bottom-accent: solid rgba(49, 55, 69, 1) 3px;
  --bw-login-switch-title-text-color: rgba(49, 55, 69, 0.5);
  --bw-login-switch-title-text-color-hover: rgba(49, 55, 69, 0.67);
  --bw-login-switch-title-text-color-accent: rgba(49, 55, 69, 1);

  --bw-login-form-close-icon-color: rgba(49, 55, 69, 1);


  --bw-login-input-placeholder-text-color: #989ba2;
  --bw-login-input-placeholder-text-color-hover: #313745;
  --bw-login-input-text-color: var(--bw-login-input-placeholder-text-color-hover); /*TODO*/
  --bw-login-input-background-color: #fcfcfd;
  --bw-login-input-background-color-hover: var(--bw-login-input-background-color); /*TODO*/
