  --bw-login-input-border-focus: var(--bw-login-input-border-hover);
  --bw-login-input-box-shadow: none;

  --bw-login-input-icon-color: var(--bw-login-input-placeholder-text-color);
  --bw-login-input-icon-color-hover: var(--bw-login-input-placeholder-text-color-hover);

  --bw-login-pass-icon-color: var(--bw-login-input-placeholder-text-color);
  --bw-login-pass-icon-color-hover: var(--bw-login-input-placeholder-text-color-hover);
  --bw-login-pass-icon-color-focus: var(--bw-login-input-placeholder-text-color-focus);

  --bw-login-button-background-color: #0070a4;
  --bw-login-button-box-shadow-accent: 0 8px 4px 0 rgba(0, 0, 0, 0.5);
