body[_theme="light"][_non-white-background="on"] bw_login {
  --bw-login-background-color: rgba(245,245,220, 0.2);
}

body[_theme="light"][_more-soft-swith-option-border-bottom="on"] bw_login {
  --bw-login-switch-option-border-bottom: solid rgba(49, 55, 69, 0.3) 3px;
  --bw-login-switch-option-border-bottom-hover: solid rgba(49, 55, 69, 0.53) 3px;
  --bw-login-switch-option-border-bottom-accent: solid rgba(49, 55, 69, 1) 3px;
}

body[_check-box-icon-transform-scale="1.15"] bw_login {
  --bw-login-check-box-icon-scale: 1.15;
}

bw_login {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;


  background-color: var(--bw-login-background-color);

  --bw-text-font-size: 16px;

  --bw-login-input-font-size: calc(var(--bw-text-font-size) + 2px);

  --bw-login-check-box-icon-scale: 1.2;

  --bw-login-control-height: 35px;
  --bw-login-input-height: var(--bw-login-control-height);
  --bw-login-input-border-radius: 3px;


  --bw-login-input-right-padding: 13px;
  /*--bw-login-input-transform-scale: calc(var(--bw-login-control-transform-scale) * 0.975);*/

  --bw-login-button-height: var(--bw-login-control-height);
  --bw-login-button-border-radius: calc(var(--bw-login-button-height) * 20 / 47);
  --bw-login-button-top-accent: -4px;
  /*--bw-login-button-transform-scale: var(--bw-login-control-transform-scale);*/

  --bw-login-link-font-size: var(--bw-text-font-size);
  /*--bw-login-link-transform-scale: var(--bw-login-control-transform-scale);*/

  --bw-login-check-box-font-size: var(--bw-text-font-size);
  --bw-login-check-box-size: calc(var(--bw-text-font-size) + 2px);
  --bw-login-check-box-title-margin-left: calc(var(--bw-login-check-box-font-size) / 2);;
  /*--bw-login-check-box-title-transofm-scale: var(--bw-login-control-transform-scale);*/

  --bw-login-switch-title-font-size: 19px;
  --bw-login-switch-title-padding-bottom: 11px;
  --bw-login-form-close-button-top: 2px;

  --bw-login-padding: 20px;
  --bw-login-form-max-width: 502px;

  --bw-login-warning-display: block;
  --bw-login-form-display: none;

  --bw-login-logo-winner-display: none;
  --bw-login-logo-icon-display: none;
  --bw-login-topspace-display: block;

  --bw-login-form-close-icon-size: 17px;
  --bw-login-form-close-button-top: 2px;

  --bw-signin-form-loginfield-margin-top: 36px;
  --bw-signin-form-passfield-margin-top: 20px;
  --bw-signin-form-bar-addon-margin-top: 20px;
  --bw-signin-form-submit-margin-top: 25px;
  --bw-signin-form-bar-anon-margin-top: 20px;

  --bw-signing-form-logininputicon-right: 17px;
  --bw-signing-form-logininputicon-width: 52px;
  --bw-signing-form-logininputicon-height: 21px;

  --bw-signing-form-passinputicon-right: 26px;
  --bw-signing-form-passinputicon-width: 26px;
  --bw-signing-form-passinputicon-closed-height: 11px;
  --bw-signing-form-passinputicon-opened-height: 19px;

  --bw-signup-form-height: 299px;
}

