@media (min-height: 320px) and (min-width: 320px) {
  [bw_login] {
    --bw-login-warning-display: none;
    --bw-login-form-display: flex;
  }
}

@media (min-height: 480px) and (min-width: 320px) {
  [bw_login] {
    --bw-login-logo-winner-display: block;
    --bw-login-control-height: 40px;
  }
}

@media (min-height: 750px) and (min-width: 750px) {
  [bw_login] {
    --bw-login-logo-icon-display: block;
    --bw-login-topspace-display: none;

    --bw-login-switch-title-font-size: 24px;
    --bw-login-switch-title-padding-bottom: 16px;
    --bw-login-form-close-button-top: 4px;

    --bw-signin-form-loginfield-margin-top: 56px;
    --bw-signin-form-passfield-margin-top: 40px;
    --bw-signin-form-bar-addon-margin-top: 32px;
    --bw-signin-form-submit-margin-top: 55px;
    --bw-signin-form-bar-anon-margin-top: 40px;

    --bw-login-control-height: 47px;
  }
  [bw_login][_font-size="16/18"] {
    --bw-text-font-size: 18px;
  }
}

[bw_login_warning] {
  color: white;
  display: var(--bw-login-warning-display);
  text-align: center;
}

/* ========================================================================= */
[bw_login] {
  justify-content: center;
}

[bw_login] > horspace {
  display: block;
  height: 100%;
  flex: 1 4 calc((var(--bw-hd-width) - var(--bw-login-form-max-width)) / 2);
  min-width: var(--bw-login-padding);
}

[bw_login_content] {
  display: flex;
  flex-direction: column;
  align-items: center;

  margin: 0;
  padding: 0;
  flex: 0 1 var(--bw-login-form-max-width);
}

[bw_login] verspace {
  display: block;
  width: 100%;
}

[bw_login_topspace] {
  display: var(--bw-login-topspace-display) !important;
  flex: 1 4 223px;
  min-height: var(--bw-login-padding);
}

[bw_login_bottomspace] {
  flex: 1 4 223px;
  min-height: var(--bw-login-padding);
}

[bw_login_overlogospace] {
  display: var(--bw-login-logo-icon-display) !important;
  flex: 1 1 56px;
  min-height: var(--bw-login-padding);
}

svg[bw_login_logoicon] {
  display: var(--bw-login-logo-icon-display) !important;
  width: 220px;
  flex: 0 0 auto;
  margin-bottom: 15px;
}

svg[bw_login_logoicon] path {
  fill: var(--bw-logo-cyan);
}

svg[bw_login_logoicon] path[bw_logo_icon_letter_w] {
  fill: var(--bw-logo-blue);
}

svg[bw_login_logowinner] {
  display: var(--bw-login-logo-winner-display) !important;
  width: 220px;
  flex: 0 0 auto;
}

[bw_login_underlogospace] {
  display: var(--bw-login-logo-winner-display) !important;
  flex: 0 1.5 96px;
  min-height: 30px;
}

svg[bw_login_logowinner] path {
  fill: var(--bw-logo-blue);
}

svg[bw_login_logowinner] path[bw_login_logowinner_letter_i_dot] {
  fill: var(--bw-logo-cyan);
}

/* =========================== common =========================== */

/* check box */

[bw_login_form] [mol_check_box] {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
}

/*[bw_login][_check-box-title-transform="on"] [bw_login_form] [mol_check_box]:hover [mol_check_box_title],
[bw_login][_check-box-title-transform="on"] [bw_login_form] [mol_check_box]:focus [mol_check_box_title] {
  transform: scale(var(--bw-login-check-box-title-transofm-scale));
}

*/
[bw_login_form] [mol_check_box]:hover [mol_check_box_title] {
  color: var(--bw-login-check-box-title-color-hover);
  font-weight: var(--bw_theme-medium-font-weight);
}

[bw_login_form] [mol_check_box]:focus [mol_check_box_title] {
  color: var(--bw-login-check-box-title-color-focus);
  font-weight: var(--bw_theme-medium-font-weight);
}


[bw_login_form] [mol_check_box_icon] {
  width: var(--bw-login-check-box-size);
  height: var(--bw-login-check-box-size);
  margin: 0;
}

[bw_login_form] [mol_check_box_title] {
  margin: 0;
  padding: 0;
  margin-left: var(--bw-login-check-box-title-margin-left);
  height: var(--bw-login-check-box-size);
  font-size: var(--bw-login-check-box-font-size);
  font-weight: var(--bw_theme-regular-font-weight);
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: var(--bw-login-check-box-title-color);
  white-space: normal;
  text-align: left;
}

[bw_login_form] [mol_check_box]:hover,
[bw_login_form] [mol_check_box]:focus {
  background-color: inherit;
}

[bw_login_form] [mol_check_box] svg {
  border: var(--bw-login-check-box-border);
  background-color: var(--bw-login-check-box-background-color);
}

[bw_login_form] [mol_check_box]:hover svg {
  border: var(--bw-login-check-box-border-hover);
  transform: scale(var(--bw-login-check-box-icon-scale));
}

[bw_login_form] [mol_check_box]:focus svg
 {
  border: var(--bw-login-check-box-border-focus);
}

[bw_login_form] [mol_check_box][mol_check_checked="true"] svg,
[bw_login_form] [mol_check_box][mol_check_checked="true"]:hover svg,
[bw_login_form] [mol_check_box][mol_check_checked="true"]:focus svg {
  background-color: var(--bw-login-check-box-background-color-checked);
  color: var(--bw-login-check-box-tick-color);
  border: var(--bw-login-check-box-border-checked);
}

/* check */

[bw_login_form] [mol_button_typed]:hover, [mol_button_typed]:hover {
  background-color: inherit;
}

/* link */

[bw_login_form] [mol_link] {
  font-weight: var(--bw_theme-regular-font-weight);
  font-size: var(--bw-login-link-font-size);
  color: var(--bw-login-link-color);
  white-space: nowrap;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  padding: 0;
}

[mol_link]:not([mol_link_current]):hover {
  background-color: inherit;
}

/*[bw_login][_link-transform="on"] [bw_login_form] [mol_link]:hover,
[bw_login][_link-transform="on"] [bw_login_form] [mol_link]:focus {
  transform: scale(var(--bw-login-link-transform-scale))
}
[bw_login][_link-outline="on"] [bw_login_form] [mol_link]:hover,
[bw_login][_link-outline="on"] [bw_login_form] [mol_link]:focus {

*/
[bw_login] [bw_login_form] [mol_link]:hover,
[bw_login] [bw_login_form] [mol_link]:focus {
  /*color: var(--bw-login-check-box-title-color-hover);*/
  /*font-weight: var(--bw_theme-medium-font-weight);*/
  text-decoration: underline;
}

/* switch */

[bw_login_form] [mol_switch] {
  box-shadow: none;
  flex-wrap: nowrap;
}


[bw_login_form] [mol_switch_option],
[bw_login_form] [mol_switch_option]:hover {
  display: flex;
  justify-content: center;
  padding: 0;
  margin: 0;
  background: inherit;
}
[bw_login_form] [mol_switch_option] {
  border-bottom: var(--bw-login-switch-option-border-bottom);
  /*border-bottom: solid rgba(100%, 100%, 100%, 0.3) 3px;*/
}
[bw_login_form] [mol_switch_option]:hover {
  border-bottom: var(--bw-login-switch-option-border-bottom-hover);
  /*border-bottom: solid rgba(100%, 100%, 100%, 0.53) 3px;*/
}

[bw_login_form] [mol_switch_option][mol_check_checked],
[bw_login_form] [mol_switch_option][mol_check_checked]:hover {
  background: inherit;
  border-bottom: var(--bw-login-switch-option-border-bottom-accent);
}

[bw_login_form] [mol_switch_option] [mol_check_title] {
  text-transform: uppercase;
  font-size: var(--bw-login-switch-title-font-size);
  font-weight: var(--bw_theme-regular-font-weight);
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: left;
  color: var(--bw-login-switch-title-text-color);
  padding-bottom: var(--bw-login-switch-title-padding-bottom);
}

[bw_login_form] [mol_switch_option]:hover [mol_check_title] {
  color: var(--bw-login-switch-title-text-color-hover);
}

[bw_login_form] [mol_switch_option][mol_check_checked] [mol_check_title] {
  color: var(--bw-login-switch-title-text-color-accent);
  font-weight: var(--bw_theme-medium-font-weight);
}

/* string */

[bw_login_form] [mol_string] {
  height: var(--bw-login-input-height);
  border-radius: var(--bw-login-input-border-radius);
  box-shadow: var(--bw-login-input-box-shadow);
  background-color: var(--bw-login-input-background-color);
  border: var(--bw-login-input-border);
}

[bw_login_form] [mol_string]:hover {
  background-color: var(--bw-login-input-background-color-hover);
  border: var(--bw-login-input-border-hover);
}

[bw_login_form] [mol_string]:focus {
  background-color: var(--bw-login-input-background-color-focus);
  border: var(--bw-login-input-border-focus);
}

[bw_login_form] [mol_string],
[bw_login_form] [mol_string]::placeholder  {
  font-size: var(--bw-login-input-font-size);
  font-weight: var(--bw_theme-regular-font-weight);
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
}

[bw_login_form] [mol_string] {
  color: var(--bw-login-input-text-color);
}

[bw_login_form] [mol_string]::placeholder  {
  color: var(--bw-login-input-placeholder-text-color);
}

[bw_login_form] [mol_string]:hover::placeholder {
  color: var(--bw-login-input-placeholder-text-color-hover);
}
[bw_login_form] [mol_string]:focus::placeholder  {
  color: var(--bw-login-input-placeholder-text-color-focus);
}

/* bar */

[bw_login_form] [mol_bar] {
  box-shadow: none;
}

/* button */

[bw_login_form] [mol_button_major] {
  padding: 0;
  margin: 0;
  height: var(--bw-login-button-height);
  object-fit: contain;
  border-radius: var(--bw-login-button-border-radius);
  background-color: var(--bw-login-button-background-color);
}

[bw_login_form] [mol_button_major]:hover ,
[bw_login_form] [mol_button_major]:focus {
  background-color: var(--bw-login-button-background-color);
  top: var(--bw-login-button-top-accent);
  box-shadow: var(--bw-login-button-box-shadow-accent)
}

/*[bw_login][_button-transform="on"] [bw_login_form] [mol_button_major]:hover ,*/
/*[bw_login][_button-transform="on"] [bw_login_form] [mol_button_major]:focus {*/
