[bw_login_form] [mol_button_major]:focus {
  transform: scale(var(--bw-login-button-transform-scale));
}*/

/* ========================================================================= */

[bw_login_form] {
  display: var(--bw-login-form-display);
  flex: 0 1 auto;
  width: 100%;
  position: relative;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  font-family: var(--bw-font-family);
  font-weight: var(--bw_theme-regular-font-weight);
}

[bw_login_form_deck] {
  width: 100%;
  flex: 0 1 auto;
  display: flex;
  flex-direction: column;
}

[bw_login_form_deck_switch] {
  flex: 0 0 auto;
}

[bw_login_form] [mol_switch_option]:nth-child(1) {
  flex: 1 1 179px;
}

[bw_login_form] [mol_switch_option]:nth-child(2) {
  flex: 1 1 323px;
}

[bw_login_form] [mol_switch_option]:last-child [mol_check_title] {
  padding-right: var(--bw-login-form-close-icon-size)
}

[bw_login_form_close] {
  position: absolute;
  top: var(--bw-login-form-close-button-top);
  right: 0;
  color: white;
  display: block;
  width: var(--bw-login-form-close-icon-size);
  height: var(--bw-login-form-close-icon-size);
}

[bw_login_form_close]:hover {
  transform: rotate(0.25turn);
}

svg[bw_login_closed_icon] {
  display: block;
  width: 100%;
  height: 100%;
}
svg[bw_login_closed_icon] path {
  fill: var(--bw-login-form-close-icon-color);
}

[bw_login_form_overanonspace] {
  flex: 0 1 40px;
  min-height: 25px;
}

[bw_login_form_bar_anon] {
  justify-content: center;
}

/* ========================================================================= */

[bw_signin_form] {
  flex: 0 1 auto;
  display: flex;
  flex-direction: column;
  padding: 0;
  margin: 0;
}

[bw_signin_form_overlogincontrolspace] {
  flex: 0 1 56px;
  min-height: 25px;
}

[bw_signin_form_logincontrol] {
  flex: 0 0 var(--bw-login-input-height);
  display: block;
  /*margin-top: var(--bw-signin-form-loginfield-margin-top);*/
  position: relative;
}

[bw_signin_form_logininput] {
  padding-right: calc(var(--bw-login-input-right-padding) + var(--bw-signing-form-logininputicon-width) + var(--bw-signing-form-logininputicon-right));
}

svg[bw_login_input_icon] {
  display: block;
  position: absolute;
  width: var(--bw-signing-form-logininputicon-width);
  height: var(--bw-signing-form-logininputicon-height);
  top: calc((var(--bw-login-input-height) - var(--bw-signing-form-logininputicon-height)) / 2);
  right: var(--bw-signing-form-logininputicon-right);
  z-index: 1;
  pointer-events: none;
}

[bw_signin_form_logincontrol] svg[bw_login_input_icon] path {
  fill: var(--bw-login-input-icon-color);
}

[bw_signin_form_logincontrol]:hover svg[bw_login_input_icon] path {
  fill: var(--bw-login-input-icon-color-hover);
}

[bw_signin_form_overpasscontrolspace] {
  flex: 0 1 40px;
  min-height: 20px;
}

[bw_pass_input] {
  /*flex: 0 0 auto;*/
  flex: 0 0 var(--bw-login-input-height);
  display: block;
  position: relative;
}

[bw_signin_form_passinput] {
  padding-right: calc(var(--bw-login-input-right-padding) + var(--bw-signing-form-passinputicon-width) + var(--bw-signing-form-passinputicon-right));
}

svg[bw_pass_input_closed_icon] {
  display: block;
  height: var(--bw-signing-form-passinputicon-closed-height);
}

svg[bw_pass_input_opened_icon] {
  display: block;
  height: var(--bw-signing-form-passinputicon-opened-height);
}

bw_pass_input path
 {
  fill: var(--bw-login-pass-icon-color);
}

bw_pass_input path.transparent {
  fill: var(--bw-login-input-background-color);
}

bw_pass_input:hover path {
  fill: var(--bw-login-pass-icon-color-hover);
}

bw_pass_input:hover path.transparent {
  fill: var(--bw-login-input-background-color-hover);
}

bw_pass_input[focused] path {
  fill: var(--bw-login-pass-icon-color-focus);
}

bw_pass_input[focused] path.transparent {
  fill: var(--bw-login-input-background-color-focus);
}


bw_pass_input:hover [mol_string] {
  background-color: var(--bw-login-input-background-color-hover);
  border: var(--bw-login-input-border-hover);
}

bw_pass_input[focused] [mol_string] {
  background-color: var(--bw-login-input-background-color-focus);
  border: var(--bw-login-input-border-focus);
}

bw_pass_input:hover [mol_string]::placeholder {
  color: var(--bw-login-input-placeholder-text-color-hover);
}
bw_pass_input[focused] [mol_string]::placeholder  {
  color: var(--bw-login-input-placeholder-text-color-focus);
}

[bw_pass_input_icon],
[bw_pass_input_icon]:hover,
[bw_pass_input_icon]:focus {
  transition-duration: 0s;
  position: absolute;
  right: var(--bw-signing-form-passinputicon-right);
  padding: 0;
  width: var(--bw-signing-form-passinputicon-width);
  height: var(--bw-signing-form-passinputicon-closed-height);
  top: calc(2px + (var(--bw-login-input-height) - var(--bw-signing-form-passinputicon-closed-height)) / 2);
  z-index: 1;
}

[bw_pass_input_icon][mol_check_checked],
[bw_pass_input_icon][mol_check_checked]:hover,
[bw_pass_input_icon][mol_check_checked]:focus {
  height: var(--bw-signing-form-passinputicon-opened-height);
  top: calc(-1px + (var(--bw-login-input-height) - var(--bw-signing-form-passinputicon-opened-height)) / 2);
}

[bw_signin_form_overaddonspace] {
  min-height: 20px;
  flex: 0 1 32px;
}

[bw_signin_form_bar_addon] {
  flex: 0 0 auto;
  justify-content: space-between;
  flex-wrap: nowrap;
}

[bw_signin_form_bar_addon] > *:first-child {
  flex: 0 1 auto;
  text-align: left;
}

[bw_signin_form_bar_addon] > *:last-child {
  flex: 1 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}
[bw_signin_form_passforgotten] {
  display: block;
  margin-left: var(--bw-text-font-size); /* https://medium.com/@iamryanyu/how-to-align-last-flex-item-to-right-73512e4e5912 */
}

[bw_signin_form_oversubmitspace] {
  flex: 0 1 55px;
  min-height: 20px;
}

[bw_signin_form_submit] {
  width: 100%;
  padding: 0;
}

/* ================================================ */

[bw_signup_form] {
  display: block;
  padding: 0;
  margin: 0;
  height: var(--bw-signup-form-height)
}
