[bw_button_close] {
  $size: 17px;
  padding: 0;
  box-sizing: border-box;
  flex: 0 0 auto;
  margin-left: auto;
  width: $size;
  height: $size;
  position: relative;

  svg {
    width: 100%;
    height: 100%;
    transition-duration: 0.15s;
    color: var(--bw_theme-bw_header-color);
    path { color: currentColor }
  }
  &:hover svg {
    transform: rotate(0.25turn);
  }
}
