[bw_modal] {
  [bw_modal_inner] {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    background: transparent;
    z-index: 10;

    [bw_modal_overlay] {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background: black;
      opacity: 0.3;
    }

    [bw_modal_form] {
      background: var(--bw_theme-workspace-block-background);
      z-index: 1;
      box-shadow: 0 8px 12px 0 rgba(0, 0, 0, 0.5);
    }
  }
}
