[mol_theme] {
  --bw_mainmenu-background-light: white;
  --bw_mainmenu-background-dark: #464f63;

  --bw_mainmenu_login-background-light: #3c4354;
  --bw_mainmenu_login-background-dark: #d8dce3;

  --bw_mainmenu_login-color-light: white;
  --bw_mainmenu_login-color-dark: #313745;

  --bw_mainmenu_list-background-light: white;
  --bw_mainmenu_list-background-dark: #464f63;

  --bw_mainmenu_list_item-hover-background-light: color(#0070a4 a(30%));
  --bw_mainmenu_list_item-hover-background-dark: color(#0070a4 a(30%));

  --bw_mainmenu_list_item_content-border-bottom-light: 1px solid #eceff5;
  --bw_mainmenu_list_item_content-border-bottom-dark: 1px solid #d8dce3;

  --bw_mainmenu_list_item_content-color-light: #313745;
  --bw_mainmenu_list_item_content-color-dark: white;

  --bw_mainmenu_list_item_content-icon-color-light: #0070a4;
  --bw_mainmenu_list_item_content-icon-color-dark: #0070a4;

  --bw_mainmenu_list_item-background-color-selected: #0070a4;
  --bw_mainmenu_list_item-background-color-selected: #0070a4;

  --bw_mainmenu_list_item_content-selected-color: white;
  --bw_mainmenu_list_item_content-selected-color: white;

  --bw_mainmenu_login_ear_inner-color-light: #d8dce3;
  --bw_mainmenu_login_ear_inner-color-dark: #464F62;
}

[bw_mainmenu] {
  $narrow-width: 64px;
  $wide-width: 290px;
  $login-height: 80px;

  height: 100%;
  flex: 0 0;
  background: var(--bw_mainmenu-background);

  display: flex;
  flex-direction: column;
  /* overflow: hidden; */
  position: relative;

  [mol_check] {
    border-radius: initial;
  }

  [mol_button] {
    line-height: normal;
  }

  &[bw_mainmenu][bw_mainmenu_width="narrow"] {
    flex: 0 0;
    box-sizing: border-box;

    [bw_mainmenu_login_title] {
      display: none;
    }

    [bw_mainmenu_list_item_inner] {
      [bw_mainmenu_list_item_content] {
        margin: 0 12px;
        display: flex;
        justify-content: flex-start;
        margin-left: 18px;
        align-items: center;
        [bw_mainmenu_list_item_title] {
          display: none
        }
        svg {
          margin: 0;
        }
      }
    }

    [bw_mainmenu_login_ear] {
      &:hover [bw_mainmenu_login_ear_inner] { left: 1px }
      [bw_mainmenu_login_ear_inner] {
        left: 0px;
      }
    }

    [bw_mainmenu_login_menu], [bw_mainmenu_list], [bw_mainmenu_list_item] {
      width: $narrow-width;
    }
  }

  $ear-width: 17px;
  [bw_mainmenu_login_ear] {
    z-index: 100;
    width: calc($ear-width + 1px);
    padding: 0;
    height: $login-height;
    box-sizing: border-box;
    top: 0px;
    position: absolute;

    &:hover [bw_mainmenu_login_ear_inner] { left: 0px }

    [bw_mainmenu_login_ear_inner] {
      background: var(--bw_mainmenu_login-background);
      position: relative;
      left: 1px;

      display: flex;
      justify-content: center;
      align-items: center;
      width: $ear-width;
      height: 100%;
      box-shadow: 4px 12px 12px 0 rgba(0, 0, 0, 0.5);

      border-top-right-radius: 3px;
      border-bottom-right-radius: 3px;
      svg {
        display: block;
        width: 11px;
        color: var(--bw_mainmenu_login_ear_inner-color);
        path { fill: currentColor }
      }
    }
  }

/*   [bw_mainmenu_loginwrapper] {
    flex: 0 0 $login-height;
    position: relative;
    width: 100%;
    overflow: hidden;
    pointer-events: none;
  }
 */
  [bw_mainmenu_login] {
    height: $login-height;
    position: relative;

    background: var(--bw_mainmenu_login-background);

    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;

    &[mol_check] { padding: 0 }
    &[mol_button_typed]:hover, &[mol_button_typed]:focus {
      background: var(--bw_mainmenu_login-background);
    }

    & > svg {
      $icon_size: 28px;
      flex: 0 0 $icon_size;
      /* width: $icon_size; */
      margin-left: 16px;
      margin-right: 8px;
      box-sizing: border-box;
      paddin: 0;
      color: var(--bw_mainmenu_login-color);

      path { fill: currentColor; }
    }

    [bw_mainmenu_login_title] {
      flex: 1 1 auto;
      font-size: calc(1rem + 2px);
      font-weight: var(--bw-font-regular-weight-inverse);
      font-style: normal;
      font-stretch: normal;
      line-height: 1.33;
      letter-spacing: normal;
      color: var(--bw_mainmenu_login-color);
      text-overflow: ellipsis; /* https://stackoverflow.com/questions/17779293/css-text-overflow-ellipsis-not-working/17783233#17783233 && 'postcss-ellipsis' */
    }
    [bw_mainmenu_login_menu] {
      position: absolute;
      left: 0;
      display: block;
      top: $login-height;
      /* width: $wide-width; */
      overflow: hidden;
      z-index: 1;
      box-shadow: 0 12px 12px 0 rgba(0, 0, 0, 0.5);
      background: var(--bw_mainmenu_list-background-inverse);
      [bw_mainmenu_list_item_content] {
        color: var(--bw_mainmenu_list_item_content-color-inverse) !important;
        font-weight: var(--bw-font-regular-weight-inverse) !important;
      }
    }

  }

  [bw_mainmenu_list_item] {
    display: block;
    $height: 53px;
    width: $wide-width;
    padding: 0;
    height: $height;
    box-sizing: border-box;
    position: relative;

    &:last-of-type [bw_mainmenu_list_item_content] {
      border-bottom: 1px solid transparent !important;
    }

    &:hover {
      background: var(--bw_mainmenu_list_item-hover-background);
    }

    [bw_mainmenu_list_item_inner] {
      display: block;
      width: 100%;
      height: $height;
      box-sizing: border-box;

      position: relative;

      [bw_mainmenu_list_item_content] {
        height: $height;
        margin: 0 16px;
        box-sizing: border-box;

        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;

        border-bottom: var(--bw_mainmenu_list_item_content-border-bottom);
        color: var(--bw_mainmenu_list_item_content-color);
        font-weight: var(--bw-font-regular-weight);
        letter-spacing: 0.3px;
        position: relative;

        svg {
          width: 28px;
          margin: 0 16px 0 1px;
          color: var(--bw_mainmenu_list_item_content-icon-color);
          &[bw_icon_home] {
            position: relative;
          }

          path { fill: currentColor }
        }

      }
    }
    &[mol_check_checked] [bw_mainmenu_list_item_inner] {
      height: calc($height + 1px);
      top: -1px;
      padding-top: 1px;
      background: var(--bw_mainmenu_list_item-background-color-selected);

      [bw_mainmenu_list_item_content] {
        color: var(--bw_mainmenu_list_item_content-selected-color);
        svg { color: inherit }
        border-bottom: 1px solid transparent;
      }
    }
  }

  [bw_mainmenu_listwrapper] {
    flex: 1 1 auto;
    overflow: hidden;
    position: relative;
  }

  [bw_mainmenu_list] {
    width: $wide-width;
    height: 100%;
    position: absolute;
    background-color: var(--bw_mainmenu_list-background);
    flex: 1 1 auto;
    overflow: auto;

    display: flex;
    flex-direction: column;


    [bw_mainmenu_list_footer] {
      flex: 0 0 auto;
      margin: auto 16px 0;
      display: flex;
      flex-direction: column;
    }
    [bw_mainmenu_list_footer_item] {
      display: flex;
      flex-direction: column;
      $margin: 24px;
      margin-bottom: $margin;
      &:first-child {
        margin-top: $margin;
      }
    }
    [bw_mainmenu_list_footer_item_label]{
      font-weight: var(--bw-font-medium-weight);
      &:after {
        content: ':';
      }
    }
  }
}

