[bw_input_combo] {
  $icon-padding-left: 8px;
  $icon-width: 11px;
  $icon-padding-right: 16px;

  $check-width: calc($icon-padding-left + $icon-width + $icon-padding-right);
  & > [bw_input] {
    padding-right: $check-width;
    &[bw_dropped_down] {
      background: var(--bw_input-focus-background);
      border: var(--bw_input-focus-border);
      box-shadow: var(--bw_input-focus-box-shadow);
    }
  }
  & > [mol_check] {
    width: $check-width;
    padding: 0 0 0 $icon-padding-left;
    svg {
      width: $icon-width;
      path { fill: currentColor }
    }
  }
  & > [mol_list] {
    box-sizing: border-box;
    position: absolute;
    z-index: 100;
    background: var(--bw_input-background);

    width: 100%;
    border-radius: 3px;
    border: var(--bw_input-focus-border);
    box-shadow: var(--bw_input-focus-box-shadow);

    & > [mol_check] {
      display: block;
      color: var(--bw_input_combo-mol_list-mol_check-color);
      padding-left: 16px;
      padding-top: 4px;
      padding-bottom: 4px;
      background: var(--mol_theme_field);

      &:hover {
        background: var(--bw_input_combo-mol_list-mol_check-hover-background);
      }
      &[mol_check_checked]{
        background: var(--bw_input_combo-mol_list-mol_check_checked-background);
      }
    }
  }
}
[mol_theme] {
  --bw_input_combo-mol_list-mol_check-color-light: #313745;
  --bw_input_combo-mol_list-mol_check-color-dark: white;

  --bw_input_combo-mol_list-mol_check-hover-background-light: #d8eeff;
  --bw_input_combo-mol_list-mol_check-hover-background-dark: color(#7fb8e0 a(50%));

  --bw_input_combo-mol_list-mol_check_checked-background-light: #7fb8e0;
  --bw_input_combo-mol_list-mol_check_checked-background-dark: #7fb8e0;
}
