/* ========================================================================= */

[bw_app] {
  font-weight: var(--bw-font-regular-weight); /* should not be placed to :root or body, does not affect otherwise */
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* ========================================================================= */

[bw_app_way_selector] {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  [mol_bar] {
    flex-direction: row;
    flex-wrap: wrap;
    background: var(--bw-panel-background);
    padding: 16px;
    [mol_link] {
      padding: 16px;
    }
  }
}

[mol_theme] {
}

/* ========================================================================= */

[bw_app_new_look] {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: stretch;
}

[bw_app_new_look]:after {
  content: '';
  text-align: center;
}

body[_viewport-width=cinema] [bw_app_new_look] {
  max-width: none;
  min-width: 2570px;
  * { display: none }
  justify-content: center;
  align-items: center;
  &:after { content: 'cinema is not supported yet for bw_app_new_look' }
}
body[_viewport-width=hd] {
  min-width: 1920px;
  max-width: 2559px;
/*   * { display: none }
  &:after { content: 'hd is not supported yet for bw_app_new_look' }
 */
}

body[_viewport-width=widescreen] [bw_app_new_look] {
  min-width: 1280px;
  max-width: 1919px;
  * { display: none }
  justify-content: center;
  align-items: center;
  &:after { content: 'widescreen is not supported yet for bw_app_new_look' }
}

body[_viewport-width=tablet-landscape] [bw_app_new_look] {
  min-width: 1024px;
  max-width: 1279px;
  * { display: none }
  justify-content: center;
  align-items: center;
  &:after { content: 'tablet-landscape is not supported yet for bw_app_new_look' }
}

body[_viewport-width=small-tablet-landscape] [bw_app_new_look] {
  min-width: 800px;
  max-width: 1023px;
  * { display: none }
  justify-content: center;
  align-items: center;
  &:after { content: 'small-tablet-landscape is not supported yet for bw_app_new_look' }
}

body[_viewport-width=tablet-portrait] [bw_app_new_look] {
  min-width: 768px;
  max-width: 799px;
  * { display: none }
  justify-content: center;
  align-items: center;
  &:after { content: 'tablet-portrait is not supported yet for bw_app_new_look' }
}

body[_viewport-width=small-tablet-portrait] [bw_app_new_look] {
  min-width: 600px;
  max-width: 767px;
  * { display: none }
  justify-content: center;
  align-items: center;
  &:after { content: 'small-tablet-portrait is not supported yet for bw_app_new_look' }
}

body[_viewport-width=mobile-landscape] [bw_app_new_look] {
  min-width: 480px;
  max-width: 599px;
  * { display: none }
  justify-content: center;
  align-items: center;
  &:after { content: 'mobile-landscape is not supported yet for bw_app_new_look' }
}

body[_viewport-width=mobile-portrait] bw_app_new_look {
  min-width: 320px;
  max-width: 479px;
  * { display: none }
  justify-content: center;
  align-items: center;
  &:after { content: 'mobile-portrait is not supported yet for bw_app_new_look' }
}

body[_viewport-width=invalid] [bw_app_new_look] {
  max-width: 319px;
  min-width: 0px;
  * { display: none }
  justify-content: center;
  align-items: center;
  &:after { content: 'min width is 320px' }
}

/* ========================================================================= */

[bw_app_old_look] {
  display: block;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  [bw_app_old_look_label]{
    font-size: larger;
    font-weight: var(--bw-font-medium-weight);
  }
  [mol_link]{
    position: absolute;
    display: block;
    padding: 10px 20px;
    left: 0;
    top: 0;
    background: var(--bw-panel-background);
    &:hover {
      background: var(--bw-panel-background) !important;
    }
  }
}

