[bw_button_major] {
  position: relative;
  cursor: pointer;

  & > * {
    position: relative;
    width: 100%;
    height: 40px;
    border-radius: 20px;
    background-color: var(--bw_theme-button-background-color);
    color: var(--bw_theme-button-color);
    font-weight: var(--bw_theme_dark-regular-font-weight);
    letter-spacing: 0.6px;

    display: flex;
    align-items: center;
    justify-content: center;
  }
  &:hover > * {
    top: -4px;
    box-shadow: var(--bw_theme-button-box-shadow);
  }
  &:active > * {
    top: 0px;
    box-shadow: none;
  }
  & > * > * {
    display: block;
  }
}
