[bw_input_checkable] {
  $icon-padding-left: 8px;
  $icon-width: 11px;
  /* $icon-padding-right: 8px; */
  $icon-padding-right: 16px;
  height: 32px;
  padding: 0;
  box-sizing: border-box;
  position: relative;
  display: block;

  $check-width: calc($icon-padding-left + $icon-width + $icon-padding-right);
  [bw_input_checkable_input] {
    height: 100%;
    padding-right: $check-width;
    text-overflow: ellipsis;
    font-size: 1rem;
    font-weight: var(--bw-font-regular-weight);
  }
  [bw_input_checkable_check] {
    position: absolute;
    top: 0;
    right: 0;

    height: 100%;
    width: $check-width;
    padding: 0 0 0 $icon-padding-left;
    box-sizing: border-box;

    z-index: 1;

    display: flex;
    justify-content: flex-start;
    align-items: center;
  }
  [bw_input_checkable_icon] {
    color: var(--bw_input_checkable_icon-color);
    width: $icon-width;
    path { fill: currentColor }
  }
}
[mol_theme] {
  --bw_input_checkable_icon-color-light: #444956;
  --bw_input_checkable_icon-color-dark: white;
}
