[mol_theme] {

}
[bw_grid2] {
  display: block;
  /* width: 100%; */
  /* height: 100%; */
  overflow: hidden;
  position: relative;
  color: #313745;
  [bw_grid2_header] {
    user-select: none;
    display: block;
    width: 100%;
    position: relative;
  }
  [bw_grid2_body] {
    display: block;
    width: 100%;
    position: relative;
    overflow: hidden;
    /* padding-left: 2px; */
  }
  [bw_grid2_row] {
    position: absolute;
    box-sizing: border-box;
    width: 100%;
  }
  [bw_grid2_row]:hover {
    box-shadow: 0 1px 2px 2px rgba(0, 0, 0, 0.2);
    z-index: 1;
  }
  [bw_grid2_cell] {
    position: absolute;
    background: #f5f8f8;
    border-right: 1px solid silver;
    border-bottom: 1px solid silver;
    display: block;
    padding: 6px 4px 0;
    box-sizing: border-box;
    height: 28px;
    box-sizing: border-box;
    font-size: 14px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    color: #313745;
    cursor: default;
  }
  [bw_grid2_moving] {
    border-left: 1px solid silver;
  }
  [bw_grid2_moving], [bw_grid2_releasing] {
    z-index: 1;
  }
  [bw_grid2_header] {
    [bw_grid2_moving], [bw_grid2_releasing] {
      z-index: 2;
    }
  }
  [bw_grid2_body] {
    [bw_grid2_col_id="blank"] {
      background: #ebedf1;
    }
  }
  [bw_grid2_moving] {
    border-top: 1px solid silver;
  }
  [bw_grid2_footer] {
    top: 28px;
    position: absolute;
    width: 100%;
    background: #f5f8f8;
    /* background: red; */
    display: flex;
  }
  [bw_grid2_note] {
    position: absolute;
    top: 0;
    width: 100%;
    border: solid 1px #979797;
    box-sizing: border-box;
    background: #f5f8f8;
    padding: 6px;
    color: #313745;
    font-size: 14px;
    overflow: auto;
  }
  [bw_grid2_usernote] {
    top: 50%;
    position: absolute;
    width: 100%;
    border: solid 1px #979797;
    font-size: 14px;
    padding: 8px;
    background: white;
    resize: none;
    &::placeholder {
      opacity: 0.7;
      color: #313745;
    }
  }
  [bw_grid2_col] {
    position: absolute;
    padding: 6px 2px 0;
    text-align: center;
    top: 0;
    height: 32px;
    box-sizing: border-box;
    background: #d8dce3;
    border-right: 1px solid silver;
    border-bottom: 1px solid silver;
    font-size: 14px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}