[mol_theme] {
  --bw_input-box-shadow-light: none;
  --bw_input-box-shadow-dark: inset 0 2px 4px 0 rgba(0, 0, 0, 0.25);

  --bw_input-border-light: 1px solid #bdc3d1;
  --bw_input-border-dark: 1px solid #d8dce3;

  --bw_input-background-light: var(--mol_theme_field-light);
  --bw_input-background-dark: var(--mol_theme_field-dark);

  --bw_input-placeholder-color-light: #989ba2;
  --bw_input-placeholder-color-dark: rgba(255, 255, 255, 0.7);

  --bw_input-disabled-background-light: var(--bw_theme_light-bw_input-background);
  --bw_input-disabled-background-dark: color(#878f9b a(50%));

  --bw_input-disabled-color-light: color(#6a6c74 a(70%));
  --bw_input-disabled-color-dark: color(#ffffff a(50%));

  --bw_input-hover-border-light: 1px solid black;
  --bw_input-hover-border-dark: 1px solid white;

  --bw_input-hover-background-light: white;
  --bw_input-hover-background-dark: #666f7f;

  --bw_input-focus-box-shadow-light: 0 0 0 1px black;
  --bw_input-focus-box-shadow-dark: 0 0 0 1px white, var(--bw_theme_dark-bw_input-box-shadow);

  --bw_input-focus-border-light: var(--bw_input-hover-border-light);
  --bw_input-focus-border-dark: var(--bw_input-hover-border-dark);

  --bw_input-focus-background-light: white;
  --bw_input-focus-background-dark: #464f63;

  --bw_input-focus-color-light: #313745;
  --bw_input-focus-color-dark: white;
}
[bw_input] {
  height: 32px;
  border-radius: 3px;
  box-shadow: var(--bw_input-box-shadow);
  border: var(--bw_input-border);
  background: var(--bw_input-background);
  &::placeholder {
    color: var(--bw_input-placeholder-color);
  }

  &:disabled {
    background: var(--bw_input-disabled-background);
    color: var(--bw_input-disabled-color);
  }
  &:hover  {
    background: var(--bw_input-hover-background);
    border: var(--bw_input-hover-border);
    &:disabled {
      background: var(--bw_input-disabled-background);
      border: var(--bw_input-border);
    }
  }
  &:focus  {
    background: var(--bw_input-focus-background);
    border: var(--bw_input-focus-border);
    box-shadow: var(--bw_input-focus-box-shadow);
    color: var(--bw_input-focus-color);
  }
}
