[mol_theme] {
  --bw_input_deep-mol_calendar-background-light: white;
  --bw_input_deep-mol_calendar-background-dark: #464f63;

  --bw_input_deep_calendar_row_cell-background-light: #0070a4;
  --bw_input_deep_calendar_row_cell-background-dark: #008ecf;

  --bw_input_deep-mol_calendar_weekdays-background-light: #d8dce3;
  --bw_input_deep-mol_calendar_weekdays-background-dark: #878f9b;

  --bw_input_deep-mol_calendar_weekdays-mol_calendar_day-color-light: color(#313745 a(90%));
  --bw_input_deep-mol_calendar_weekdays-mol_calendar_day-color-dark: #d8dce3;

  --bw_input_deep-mol_calendar_weeks-mol_calendar_day-color-light: #b2b7bf;
  --bw_input_deep-mol_calendar_weeks-mol_calendar_day-color-dark: #b2b7bf;

  --bw_input_deep_calendar_checkable-hover-background-light: color(#f0f1f4 a(30%));
  --bw_input_deep_calendar_checkable-hover-background-dark: color(#878f9b a(30%));

  --bw_input_deep_calendar_checkable-hover-border-light: 1px solid color(#008ecf a(30%));
  --bw_input_deep_calendar_checkable-hover-border-dark: 1px solid color(#008ecf a(30%));

  --bw_input_deep-mol_calendar_selected-background-light: #f0f1f4;
  --bw_input_deep-mol_calendar_selected-background-dark: #878f9b;

  --bw_input_deep-mol_calendar_selected-border-light: 1px solid #008ecf;
  --bw_input_deep-mol_calendar_selected-border-dark: 1px solid #008ecf;

  --bw_input_deep-mol_calendar_day-mol_check-color-light: #313745;
  --bw_input_deep-mol_calendar_day-mol_check-color-dark: white;
}
[bw_input_deep] {
  position: relative;
  height: 32px;

  display: flex;
  flex-wrap: nowrap;
  align-items: center;

  [bw_input_deep_input] {
    flex: 1 0 90px;
  }
  [bw_input_deep_output] {
    flex: 1 1 auto;
    white-space: nowrap;
    text-align: center;
    margin-right: 8px;
    margin-left: 8px;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  [bw_input_deep] horspace {
    flex: 1 1 21px;
  }
  [bw_input_deep_button] {
    flex: 0 0 auto;
    svg {
      width: 28px;
      height: 28px;
    }
    svg path { fill: #0070a4 }
  }

  $mol_calendar_day-size: 31px;
  $mol_calendar-width: calc($mol_calendar_day-size * 7);
  [mol_calendar] {
    width: $mol_calendar-width;
    box-sizing: border-box;
    border-collapse: separate;
    position: absolute;
    top: 0;
    font-family: inherit;
    background: var(--bw_input_deep-mol_calendar-background);
    z-index: 2;
    box-shadow: 0 8px 12px 0 rgba(0, 0, 0, 0.5);
    letter-spacing: 0.6px;
    $footer-header-height: 33px;

    [bw_input_deep_calendar_row_cell] {

      $height: $footer-header-height;
      height: $height;
      position: relative;

      & > * {
        width: 100%;
        height: 100%;
        box-sizing: border-box;
        color: white;
        font-weight: var(--bw-font-regular-weight-dark);

        background: var(--bw_input_deep_calendar_row_cell-background);
      }

      [bw_input_deep_calendar_titlecontent] {

        display: flex;

        [bw_input_deep_calendar_titletext] {
          flex: 1 1 auto;
          padding: 0;
          color: inherit;
          display: flex;
          justify-content: center;
          align-items: center;
          text-transform: capitalize;
        }

        [bw_input_deep_calendar_prevmonth], [bw_input_deep_calendar_nextmonth] {
          padding: 0;
          flex: 0 0 calc($height - 1px);
          height: calc($height - 1px);
          box-sizing: border-box;

          display: flex;
          justify-content: center;
          align-items: center;
          color: inherit;
          svg { width: 9px }
          svg path { fill: currentColor }
        }
      }

      [bw_input_deep_calendar_dropfilterbutton] {
        text-align: center;
        border-radius: initial;
      }
    }

    [mol_calendar_day] {
      padding: 0;
      box-sizing: border-box;
      width: var($mol_calendar_day-size);
    }

    [mol_calendar_weekdays] {
      background: var(--bw_input_deep-mol_calendar_weekdays-background);

      [mol_calendar_day] {
        text-transform: capitalize;
        $height: 24px;
        height: calc($height + 1px);
        line-height: calc($height - 4px);
        padding-top: 4px;
        color: var(--bw_input_deep-mol_calendar_weekdays-mol_calendar_day-color);
      }
    }

    [mol_calendar_weeks] {
      [mol_calendar_day] {
        height: $mol_calendar_day-size;
        border: 1px solid transparent;
        vertical-align: middle;
        color: var(--bw_input_deep-mol_calendar_weeks-mol_calendar_day-color);
        position: relative;
      }
    }

    [bw_input_deep_calendar_monthrow] {
      display: table-row;

      [bw_input_deep_calendar_month] {
        display: table-cell;

        $first-col-width: calc($mol_calendar-width / 2);
        $second-col-width: calc($mol_calendar-width - $first-col-width);
        &:first-child { width: $first-col-width }
        &:last-child { width: $second-col-width }
        height: 30px;
        box-sizing: border-box;

        text-transform: capitalize;
        text-align: center;

        vertical-align: middle;
        padding: 0;
        color: var(--bw_input_deep-mol_calendar_weeks-mol_calendar_day-color);

        position: relative;
      }
    }

    [mol_calendar_day], [bw_input_deep_calendar_month] {
      &[mol_calendar_ghost] {
        opacity: 0;
      }
      &[bw_input_deep_calendar_checkable]:hover {
        background: var(--bw_input_deep_calendar_checkable-hover-background);
        border: var(--bw_input_deep_calendar_checkable-hover-border);
      }
      &[mol_calendar_selected],
      &[mol_calendar_selected]:hover {
        background: var(--bw_input_deep-mol_calendar_selected-background);
        border: var(--bw_input_deep-mol_calendar_selected-border);
      }
      [mol_check] {
        width: 100%;
        height: 100%;
        padding: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        box-sizing: border-box;
        color: var(--bw_input_deep-mol_calendar_day-mol_check-color);
      }
    }

  }
}
