
[mol_theme] {
  --bw_block_selected-box-shadow-light: 0 15px 30px 0 rgba(132, 132, 132, 0.8);
  --bw_block_selected-box-shadow-dark: 0 15px 30px 0px #464f63;

  --bw_theme-bw_block_header-color-light: #313745;
  --bw_theme-bw_block_header-color-dark: white;
}

[bw_block] {
  background: var(--bw-panel-background);
  border-radius: 2px;

  &[bw_block_selected] {
    box-shadow: var(--bw_block_selected-box-shadow);
  }
  [bw_block_header] {
    color: var(--bw_theme-bw_block_header-color);
  }
}

