
[mol_theme] {
  --bw_settings_menu-mol_check-border-light: 4px solid color(#313745 a(20%));
  --bw_settings_menu-mol_check-border-dark: 4px solid rgba(255, 255, 255, 0.2);

  --bw_settings_menu-mol_check-color-light: #313745;
  --bw_settings_menu-mol_check-color-dark: white;

  --bw_settings_menu-mol_check-checked-border-light: 4px solid color(#313745 a(50%));
  --bw_settings_menu-mol_check-checked-border-dark: 4px solid white;

  --bw_settings_profile_block_greentext-color-light: #aaacb1;
  --bw_settings_profile_block_greentext-color-dark: #d8dce3;

  --bw_settings_blacklist_block_grid-color-light: #6a6c74;
  --bw_settings_blacklist_block_grid-color-dark: white;

  --bw_settings_blacklist_block_grid-th-background-light: #d8dce3;
  --bw_settings_blacklist_block_grid-th-background-dark: #6e7581;

  --bw_settings_blacklist_block_grid-td-background-light: #f0f1f4;
  --bw_settings_blacklist_block_grid-td-background-dark: #878f9b;

  --bw_settings_blacklist_block_grid-bw_icon_search-color-light: #313745;
  --bw_settings_blacklist_block_grid-bw_icon_search-color-dark: color(white a(70%));

  --bw_settings_blacklist_block_grid-button-color-light: color(#6a6c74 a(70%));
  --bw_settings_blacklist_block_grid-button-color-dark: color(white a(70%));

  --bw_settings_blacklist_block_add_modal_headercontent-color-light: #313745;
  --bw_settings_blacklist_block_add_modal_headercontent-color-dark: white;

  --bw_settings_blacklist_block_add_modal-background-light: white;
  --bw_settings_blacklist_block_add_modal-background-dark: #464f63;

  --bw_settings_blacklist_block_grid-bw_grid_row_cursor-background-light: rgba(125, 201, 255, 0.7);
  --bw_settings_blacklist_block_grid-bw_grid_row_cursor-background-dark: rgba(125, 201, 255, 0.7);

  --bw_settings_blacklist_block_grid-bw_grid_row_selected-background-light: rgba(125, 201, 255, 0.6);
  --bw_settings_blacklist_block_grid-bw_grid_row_selected-background-dark: rgba(125, 201, 255, 0.6);

  --bw_settings_blacklist_block_grid-bw_button_major-color-light: color(#6a6c74 a(70%));
  --bw_settings_blacklist_block_grid-bw_button_major-color-dark: color(white a(70%));
}
[bw_settings] {
  /* flex: 1 1 calc(var(--bw-hd-width) - var(--bw-main-menu-max-width)); */
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  $bw_settings_padding_left: 0px;
  $bw_settings_padding_right: 0px;
  /* $bw_settings_padding_left: 16px; */
  /* $bw_settings_padding_right: 16px; */
  padding: 0 $bw_settings_padding_right 0 $bw_settings_padding_left;
  box-sizing: border-box;

  [bw_block] {
    & {
      margin-left: 16px;
    }

    &:last-child {
      margin-right: 16px;
    }
  }

  [bw_settings_menu] {
    $left_space: calc(45px - $bw_settings_padding_left);
    /* margin: 24px 0 26px $left_space; */
    margin: 24px 0 10px $left_space;
    flex: 0 0 auto;
    box-shadow: none;

    [mol_check] {
      text-transform: uppercase;
      font-size: 18px;
      padding: 0 16px;

      border-bottom: var(--bw_settings_menu-mol_check-border);
      line-height: normal;
      height: 21px;
      padding-bottom: 5px;
      box-sizing: content-box;
      margin-bottom: 0;
      font-weight: var(--bw-font-regular-weight);
      color: var(--bw_settings_menu-mol_check-color);

      &:hover {
        /* TODO */
      }

      &[mol_check_checked] {
        background-color: inherit;
        font-weight: var(--bw-font-medium-weight);
        border-bottom: var(--bw_settings_menu-mol_check-checked-border);
      }
    }
  }

  [bw_settings_content] {
    display: block;
    padding-right: 0;
    overflow: auto;
    position: relative;

    [bw_block_inner] {
      display: flex;
      flex-direction: column;
      [bw_block_header] {
        flex: 0 0 93px;
        padding-top: 32px;
        box-sizing: border-box;

        display: flex;
        flex-direction: column;
        justify-content: flex-start;

        [bw_block_caption] {
          text-transform: uppercase;
          height: 21px;
          font-weight: var(--bw-font-medium-weight);
          font-size: 18px;
          box-sizing: content-box;
        }
      }
      [bw_block_content] {
        flex: 1 1 auto;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        [bw_field] {
          display: flex;
          justify-content: space-between;
        }
      }
      [bw_block_buttonbar] {
        flex: 0 0 103px;
        box-sizing: border-box;
        box-shadow: none;
        display: flex;
        align-items: center;
        justify-content: center;

        & > *:first-child {
          width: 200px;
          box-sizing: border-box;
        }
      }
    }

    [bw_settings_narrow] {
      display: flex;
      flex-wrap: nowrap;
      justify-content: space-between;
      margin-top: 16px;

      [bw_block] {
        $block-min-width: 520px;
        $block-max-width: 597px;
        flex: 1 0 $block-min-width;
        max-width: 598px;
        height: 420px;
        box-sizing: border-box;
        display: flex;
        justify-content: flex-start;

        &[bw_settings_search_block] {
          $block-horspace-min-width: 16px;
          $block-horspace-max-width: 32px;
          & > horspace {
            flex: 1 0 $block-horspace-min-width;
            max-width: $block-horspace-max-width;
            box-sizing: border-box;
            display: block;
          }
          [bw_block_inner] {
            $inner-min-width: calc($block-min-width - 2 * $block-horspace-min-width);
            $inner-max-width: calc($block-max-width - 2 * $block-horspace-max-width);
            flex: 1 0 $inner-min-width;
            max-width: $inner-max-width;
            box-sizing: border-box;
            $field-control-min-width: 335px;
            $field-control-max-width: 354px; /* TODO: use in calc for flex of & > *:last-child */

            [bw_field] {
              & > *:first-child {
                flex: 1 0 calc($inner-min-width - $field-control-min-width);
              }
              & > *:last-child {
                flex: 0.731 0 $field-control-min-width;
              }
            }
          }
        }

        &[bw_settings_personal_block] {
          $block-left-space-width: 32px;
          $block-right-space-min-width: 36px;
          $block-right-space-max-width: 113px;

          & > horspace {
            box-sizing: border-box;
            display: block;
          }

          [bw_block_leftspace] {
            flex: 0 0 $block-left-space-width;
          }
          [bw_block_rightspace] {
            flex: 1 0 $block-right-space-min-width;
            max-width: $block-right-space-max-width;
          }
          [bw_block_inner] {
            $inner-min-width: calc($block-min-width - ($block-left-space-width + $block-right-space-min-width));
            $inner-max-width: calc($block-max-width - ($block-left-space-width + $block-right-space-max-width));
            flex: 1 0 calc($block-min-width - ($block-left-space-width + $block-right-space-min-width));
            max-width: calc($block-max-width - ($block-left-space-width + $block-right-space-max-width));
            box-sizing: border-box;
            $field-control-min-width: 354px;
            $field-control-max-width: 354px; /* TODO: use in calc for flex of & > *:last-child */

            [bw_field] {
              & > *:first-child {
                flex: 1 0 calc($inner-min-width - $field-control-min-width);
              }
              & > *:last-child {
                flex: 0 0 $field-control-min-width;

                &[bw_settings_personal_block_passcontrol] {
                  display: flex;
                  align-items: center;
                  flex-wrap: nowrap;
                  [bw_input] {
                    flex: 1 1 auto;
                  }
                  [mol_link] {
                    flex: 0 0 auto;
                    margin-left: 16px;
                    font-size: calc(1rem - 2px);
                    padding: 0;
                  }
                }
              }
            }
          }
        }

        &[bw_settings_profile_block] {
          $block-left-space-width: 32px;
          $block-right-space-min-width: 32px;
          $block-right-space-max-width: 109px;
          [bw_block_leftspace] {
            display: block;
            flex: 0 0 $block-left-space-width;
            box-sizing: border-box;
          }
          [bw_block_rightspace] {
            display: block;
            flex: 1 0 $block-right-space-min-width;
            max-width: $block-right-space-max-width;
            box-sizing: border-box;
          }
          [bw_block_inner] {
            $inner-min-width: calc($block-min-width - ($block-left-space-width + $block-right-space-min-width));
            $inner-max-width: calc($block-max-width - ($block-left-space-width + $block-right-space-max-width));
            flex: 1 0 calc($block-min-width - ($block-left-space-width + $block-right-space-min-width));
            max-width: calc($block-max-width - ($block-left-space-width + $block-right-space-max-width));
            box-sizing: border-box;
            $field-control-min-width: 317px;
            $field-control-max-width: 317px; /* TODO: use in calc for flex of & > *:last-child */
            [bw_block_header] {
              flex: 0 0 77px;
            }
            [bw_field] {
              & > *:first-child {
                flex: 1 0 calc($inner-min-width - $field-control-min-width);
              }
              & > *:last-child {
                flex: 0 0 $field-control-min-width;
              }
            }

            [bw_settings_profile_block_greentext] {
              line-height: 24px;
              color: var(--bw_settings_profile_block_greentext-color);
              font-weight: var(--bw-font-light-weight);
              & > * {
                display: block;
              }
              margin-bottom: 24px;
            }
          }
        }
      }
    }

    [bw_settings_wide] {
      display: block;
      width: 100%;
      box-sizing: border-box;
      margin: 16px 0;

      [bw_settings_blacklist_block] {
        flex: 1 1 auto;

        display: flex;
        [bw_block_header] {
          flex: 0 0 85px;
        }

        $block-space-width: 32px;
        & > horspace {
          display: block;
          flex: 0 0 $block-space-width;
          box-sizing: border-box;
        }

        [bw_block_inner] {
          display: flex;
          flex-direction: column;
          box-sizing: border-box;
          flex: 1 1 auto;
        }

        [bw_settings_blacklist_block_add_modal_form] {
          width: auto;
          height: auto;
          padding: 0 16px;
          box-sizing: border-box;
          box-shadow: 0 8px 12px 0 rgba(0, 0, 0, 0.5);
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          background: var(--bw_settings_blacklist_block_add_modal-background);
          [bw_settings_blacklist_block_add_modal_headerbar] {
            flex: 0 0 77px;
            padding-bottom: 12px;
            box-sizing: border-box;
            display: flex;
            flex-direction: column;
            justify-content: flex-end;

            [bw_settings_blacklist_block_add_modal_headercontent] {
              display: flex;
              align-items: center;
              justify-content: flex-start;
              color: var(--bw_settings_blacklist_block_add_modal_headercontent-color);

              [bw_settings_blacklist_block_add_modal_headertitle] {
                flex: 0 0 auto;
                font-size: 18px;
              }
            }
          }
          [bw_settings_blacklist_block_add_modal_content] {
            & > * {
              margin: 16px 0;
              display: block;
            }
            [bw_settings_blacklist_block_add_modal_phonenumber] {
              width: 100%;
              height: 32px;
            }
            [bw_settings_blacklist_block_add_modal_phonedescription] {
              $width: 502px;
              width: $width;
              min-height: 32px;
              height: 126px;
              min-width: $width;
              max-width: calc($width * 2);
              font-family: inherit;
              font-stretch: inherit;
              textarea {
                height: 100%;
                font-family: inherit;
                font-stretch: inherit;
              }
            }
          }
          [bw_settings_blacklist_block_add_modal_buttonbar] {
            flex: 0 0 56px;
            display: flex;
            justify-content: center;
            align-items: flex-start;

            [bw_settings_blacklist_block_add_modal_button] {
              width: 200px;
              height: 40px;
            }
          }
        }

        [bw_settings_blacklist_block_content] {
          margin: 0;
          flex: 1 1 auto;
          [bw_settings_blacklist_block_grid] {
            flex: 0 0 auto;
            box-shadow: none;
            margin: 0;

            [mol_grid_table] {
              width: 100%;
              border-collapse: separate;
              border-spacing: 0 1px;
              color: var(--bw_settings_blacklist_block_grid-color);
              th {
                background: var(--bw_settings_blacklist_block_grid-th-background);
              }
              td {
                background: var(--bw_settings_blacklist_block_grid-td-background);
              }
              th, td {
                padding: 0 8px; /* ??? */
                box-shadow: none;
              }
              th {
                padding: 0 8px;
                text-transform: uppercase;
                font-size: 14px;
                font-weight: var(--bw-font-medium-weight);
                text-align: center;
                [bw_grid_column_filterable] > [mol_check] {
                  padding: 0;
                  color: inherit;
                }

                [bw_icon_search] {
                  position: relative;
                  width: 18px;
                  top: 1.5px;
                  margin-left: 16px;
                  color: var(--bw_settings_blacklist_block_grid-bw_icon_search-color);
                  path { fill: currentColor }
                }
              }
              [bw_grid_row_selected] td {
                background: var(--bw_settings_blacklist_block_grid-bw_grid_row_selected-background);
              }
              [bw_grid_row_cursor] td {
                background: var(--bw_settings_blacklist_block_grid-bw_grid_row_cursor-background);
              }
              tr > * {
                box-sizing: border-box;
              }
              tr > *:nth-child(1) {
                padding: 0;
                width: 78px;
                text-align: center;
                [mol_check_box] svg {
                  width: 14px;
                  height: 14px;
                }
              }
              tr > *:nth-child(2) {
                padding-left: 24px;
                width: 184px;
              }
              tr > *:nth-child(3) {
                padding-left: 40px;
                width: 877px;
              }
              tr > *:nth-child(4) {
                padding-left: 40px;
                width: 304px;
              }
              tr > *:nth-child(5) {
                width: 90px;
                text-align: center;
                [bw_button_major] {
                  width: 40px;
                  & > * {
                    background-color: transparent;
                  }
                  &:hover > * {
                    top: -2px;
                    box-shadow: 0 2px 0px 0 rgba(0, 0, 0, 0.25);
                  }
                  svg {
                    height: 18px;
                    color: var(--bw_settings_blacklist_block_grid-bw_button_major-color);
                    path { fill: currentColor }
                  }
                }
              }
            }
          }
        }
        [bw_block_buttonbar] {
          justify-content: flex-start;
          align-items: flex-start;
          padding-top: 32px;
          flex: 1 1 100px;
          & > *:first-child {
            margin-right: 16px;
            width: 160px;
          }
          & > *:last-child {
            width: 40px;
            svg {
              height: 21px;
              path { fill: currentColor }
            }
          }
        }
      }

    }
  }
}
