[bw_easter_panel] {
  position: absolute;
  right: 0px;
  top: 0px;
  width: 220px;
  height: 42px;
  opacity: 0;
  background-color: var(--bw-panel-background);

  [bw_easter_panel_nav] {
    display: none;
  }
  &[shown] {
    opacity: 1;
    width: auto;
    height: auto;
    z-index: 1;
    height: 100%;

    [bw_easter_panel_nav] {
      height: 100%;
      display: block;
      background-color: var(--bw-panel-background);

      [bw_easter_panel_nav_switchcell] {
        padding: 0;
        [bw_easter_panel_nav_switch] {
          flex-wrap: nowrap;
          [mol_check] {
            flex: 1 1 100px;
            justify-content: center;
          }
        }
      }
    }
  }
}

